(function(){
'use strict';
window.addEventListener('load', ()=>{
    //isotope
    let elem = document.querySelector('.categories__items'),
    btns = document.querySelectorAll('.links__link');
    let iso = new Isotope( elem, {
      itemSelector: '.categories__item',
      layoutMode: 'fitRows'
    });
    btns.forEach(btn => {
        btn.addEventListener('click', (e)=>{
            e.preventDefault();
            let filter = e.target.getAttribute('data-filter');
             filter = filter == '*' ? filter: `.${filter}`;
            let active = document.querySelector('.active');
            active.classList.remove('active');
            btn.classList.add('active');  
            iso.arrange({filter: filter});
        })
    });
    //ratings
    let starRatingControl = new StarRating('.star-rating',{
        maxStars: 5,
        showText: false,
        showText: false,
        showText: false,
        classname: "gl-star-rating",
        initialText: "Select a Rating"
    });
    starRatingControl.destroy();
    starRatingControl.rebuild();
    //slider
    $(document).ready(function()
    {  $('.slider').slick({
        dots: true,
        arrows: true,
        prevArrow: `<button type="button" class="slider__arrow slider__arrow--prev">  Previous </button>`,
        nextArrow: `<button type="button" class="slider__arrow slider__arrow--next">  Next  </button>`,
        dotsClass: 'slider__dots'
    });
   //animate.css
    function isInViewport(elem) {
    let bounding = elem.getBoundingClientRect();
        return (
            bounding.top >= 0 &&
            bounding.left >= 0 &&
            bounding.top <= (window.innerHeight || document.documentElement.clientHeight) &&
            bounding.right <= (window.innerWidth || document.documentElement.clientWidth)
        );
    };
    function checkItems()
    {
        let items = document.querySelectorAll('.animate');
        items.forEach(item=>{
            if(isInViewport(item))
            {
                let animateMode = item.getAttribute('data-animate');
                let delay = item.getAttribute('data-delay');
                if (delay==null)
                {
                    item.classList.add('animate__animated', 'animate__'+ animateMode);
                }else{
                    item.classList.add('animate__animated', 'animate__'+ animateMode, 'animate__'+ delay);
                }
                item.classList.remove('animate');
            }
        }) 
    }
    checkItems();
    window.addEventListener('scroll', function(e)
    {
        checkItems()   
    })
});
})
})();